/* OperatorControlPanel.css */
.operator-control-panel {
  background: linear-gradient(45deg, 
  #b3e0ff,  /* 浅蓝色 */
  hsl(216, 100%, 80%),  /* 更浅的蓝色 */
  #699dcd,  /* 更淡的蓝色 */
  #4d6c8e   /* 极浅的蓝色 */
);
    border-radius: 8px; /* 设置边框圆角 */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /* 设置阴影效果 */
    padding: 16px; /* 设置内边距 */
    width: 260px;
    height: 320px;
  }
  
  .settings-header {
    display: flex;
    align-items: center;
    justify-content: start;
    font-weight: bold;
    font-size: 28px;
    margin-bottom: 10px; 
  }

  .operator-control-panel h4 {
    display: flex; /* 使用flex布局 */
    font-weight: normal;
    align-items: center; /* 垂直居中 */
    font-size: 24px;; /* 设置字体大小 */
    color: #000000; /* 设置字体颜色 */
    margin-top: 1px;
    margin-bottom: 4px;
  }
  
  .operator-control-panel h3 .anticon {
    margin-right: 8px; /* 在图标和文本之间添加一些间距 */
  }

  .operator-control-panel .ant-btn {
    font-size: 18px; /* 设置按钮内字体大小 */
    color: rgb(0, 0, 0) !important; /* 设置按钮内字体颜色 */
    background-color: #9cf3f2; /* 设置按钮背景颜色 */
    border-color: rgb(223, 223, 223); /* 设置按钮边框颜色 */
    margin: 8px 24px; /* 设置按钮间的边距 */
    margin-top: 8px;
    border-radius: 4px; /* 设置按钮边框圆角 */
    width: 100px; /* 设置按钮宽度 */
    height: 40px; /* 设置按钮高度 */
    display: inline-flex; /* 使用flex布局使宽度和高度生效 */
    align-items: center; /* 利用flex布局垂直居中文本 */
    justify-content: center; /* 水平居中文本 */
    /* 移除line-height或保证它和height一致 */
    line-height: 40px; /* 如果不使用flex布局，可以尝试设置和height相同的line-height */
    padding: 0; /* 移除padding，由height控制按钮高度 */
    
  }

  .operator-control-panel .button-container {
    display: flex;
    justify-content: center; /* 水平居中所有子元素 */
    align-items: center; /* 垂直居中所有子元素 */
    flex-direction: column; /* 子元素垂直排列 */
    margin-top: 55px;
    
  }

  /* 如果按钮需要在容器中水平居中，且容器有多个按钮水平排列 */
  .operator-control-panel .button-container-row {
    display: flex;
    justify-content: space-around; /* 水平分布子元素 */
    align-items: center; /* 垂直居中所有子元素 */
    flex-wrap: wrap; /* 允许按钮换行 */
    gap: 16px; /* 按钮之间的间隔 */
  }
  
  .operator-control-panel .ant-btn-primary:hover,
  .operator-control-panel .ant-btn-primary:focus {
    background-color: #f3f3f3; /* 鼠标悬浮或聚焦时改变背景颜色 */
    border-color: #ffffff; /* 鼠标悬浮或聚焦时改变边框颜色 */
  }
  
  .operator-control-panel .ant-btn-dashed {
    background: transparent; /* 透明背景 */
    color: #1890ff; /* 设置虚线按钮字体颜色 */
    border-color: #1890ff; /* 设置虚线按钮边框颜色 */
  }
  
  .operator-control-panel .ant-btn-dashed:hover {
    background: rgba(255, 255, 255, 0.1); /* 鼠标悬浮时虚线按钮的背景颜色 */
  }

  .pagination-container {
    margin-top: auto; /* 自动将分页器推到容器底部 */
  }
    
  .operator-grid {
    display: grid;
    flex-direction: column; /* 确保子元素按列排布 */
    justify-content: space-between; /* 分页器置底 */
    height: 100%; /* 或者设置成足够的高度以适应所有子元素 */
    grid-template-columns: repeat(3, 1fr); /* 每行三个元素 */
    gap: 5px;
    margin-bottom: 10px; /* 与分页控件的间隔 */
    padding-left: 0px;
    height: 150px;
  }

  
  .back-button {
    font-size: 18px; /* 设置字体大小 */
    height: 40px; /* 设置按钮高度 */
    display: flex;
    justify-content: center; /* 水平居中 */
  }
  
  
  .operator-card {
    display: flex; /* 使用flex布局使内容居中 */
    justify-content: center; /* 水平居中 */
    align-items: center; /* 垂直居中 */
    width: 84px;
    height: 40px; /* 设置固定高度 */
    background-color: #2b4863; /* 设置背景颜色 */
    color: white; /* 设置字体颜色 */
    font-size: 18px; /* 设置字体大小 */
    border-radius: 8px; /* 设置边框圆角 */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15); /* 设置阴影 */
    margin-bottom: 5px;
    cursor: pointer; /* 鼠标悬浮时的指针样式 */
  }

  
  .operator-card-inactive {
    display: flex; /* 使用flex布局使内容居中 */
    justify-content: center; /* 水平居中 */
    align-items: center; /* 垂直居中 */
    width: 84px;
    height: 40px; /* 设置固定高度 */
    background-color: #f0f0f0; /* 设置背景颜色 */
    color: rgb(0, 0, 0); /* 设置字体颜色 */
    font-size: 18px; /* 设置字体大小 */
    border-radius: 8px; /* 设置边框圆角 */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15); /* 设置阴影 */
    margin-bottom: 5px;
    cursor: pointer; /* 鼠标悬浮时的指针样式 */
    opacity: 0.5; /* Make the card appear faded */
  }

  .operator-card.selected {
    border: 2px solid #1890ff; /* 选中时的边框颜色 */
    background-color: #60a3e2; /* 设置背景颜色 */
  }

  .operator-control-panel .ant-pagination-item a {
    color: #000000; /* 未选中页数的文字颜色 */
  }
  
  /* 设置选中页脚的背景和字体颜色 */
  .operator-control-panel .ant-pagination-item-active a {
    background-color: #b3bfc6; /* 选中页数的背景颜色 */
    color: #000000; /* 选中页数的文字颜色 */
    border-color: #b3bfc6; /* 选中页数的边框颜色 */
  }
  
  .operator-control-panel .ant-pagination-item-active {
    border-color: #b3bfc6; /* 保持边框颜色一致性 */
  }
  
  .operator-control-panel .ant-btn {
    margin-left: 0; /* 如果需要，调整按钮的左边距 */
    font-size: 20px;
    color: rgb(255, 255, 255);
  }
  
  .operator-control-panel .ant-pagination {
    margin-right: 0; /* 如果需要，调整分页器的右边距 */
  }