.settings-container {
  background: linear-gradient(45deg, 
  #b3e0ff,  /* 浅蓝色 */
  hsl(216, 100%, 80%),  /* 更浅的蓝色 */
  #699dcd,  /* 更淡的蓝色 */
  #4d6c8e   /* 极浅的蓝色 */);
  color: black;
  width: 260px;
  height: 480px;
  padding: 16px;
  border-radius: 4px;
}

.settings-header {
  display: flex;
  align-items: center;
  justify-content: start;
  font-weight: bold;
  font-size: 28px;
}

/* 面板内容通用样式 */
.site-collapse-custom-content label {
  color: #000000; /* 标签文字颜色 */
  display: block; /* 标签占据整行 */
  margin-bottom: 10px; /* 标签和选择框之间的间距 */
  margin-top: 10px;
  font-size: 24px; /* 标签文字大小 */
}

/* 选择框样式 */
.site-collapse-custom-content .ant-select-selector {
  background-color: #9cf3f2 !important; /* 选择框背景颜色 */
  padding-top: 8px; /* 内边距上 */
  padding-bottom: 8px; /* 内边距下 */
  font-size: 18px; /* 选择框文字大小 */
}

/* 选择框高度调整 */
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
  height: 35px; /* 选择框高度 */
  line-height: 30px; /* 文本行高，以垂直居中 */
}

/* 滑块样式 */
.ant-slider-track,
.ant-slider-dot,
.ant-slider-handle {
  background-color: rgb(133, 206, 135) !important; /* 滑块颜色 */
}

.ant-slider-rail {
  background-color: rgba(255, 255, 255, 0.3) !important; /* 滑块轨道背景色 */
}

/* 内容字体大小 */
.ant-collapse-content {
  font-size: 24px; /* 内容字体大小 */
}


.ant-switch-inner-checked,.ant-switch-inner-unchecked{
    font-size: 20px !important;
}


.ant-switch-handle{
  width: 24px !important;
  height: 24px !important;
}

.ant-switch{
  height: 28px !important;
}