/* DatabaseSchemaGraph.css */
.database-schema-graph-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .node-info-container {
    margin-top: 10px;
  }
  .node-info {
    border: 1px solid #ddd;
    margin-top: 5px;
    padding: 10px;
    text-align: center; /* 居中对齐 */
  }
  
  .node-info h3 {
    margin-bottom: 10px; /* 大标题下方留白 */
    margin-top: 0; /* 与上方元素距离为0 */
  }
  
  .info-table {
    border-collapse: collapse; /* 合并边框 */
    width: 100%; /* 宽度100% */
  }
  
  .info-table th,
  .info-table td {
    border: 1px solid #ddd; /* 单元格边框 */
    padding: 6px; /* 单元格内边距 */
    width: 150px; /* 单元格宽度 */
  }
  
  .info-table th {
    background-color: #f2f2f2; /* 表头背景颜色 */
  }

  .data-title {
    font-family: 'Arial', sans-serif; /* 修改字体 */
    font-size: 32px; /* 修改字体大小 */
    color: #333; /* 修改字体颜色 */
    margin-top: 0px; /* 修改上边距 */
    margin-left: 10px;
    margin-bottom: 0px; /* 修改下边距 */
  }

  .tooltip {
    position: absolute;
    background-color: rgba(235, 242, 245, 0.9);
    border: 1px solid #ccc;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    width: 200px;
    height: 80px;
    padding: 8px 12px;
    border-radius: 4px;
    pointer-events: none;
    transition: opacity 0.2s ease-in-out;;
    opacity: 0;
    z-index: 100;
    backdrop-filter: blur(5px); /* 背景虚化效果 */
  }
  
  .tooltip.show {
    visibility: visible;
    opacity: 1;
  }
  
  .tooltip-title {
    margin-right: 10px;
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }
  
  .tooltip-content {
    font-size: 16px;
    color: #010101;
    margin-bottom: 15px;
  }
  